___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
    .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
    }
   
    .app-bar .app-bar__brand h1 {
      font-size: 1.5em;
    }
   
    .app-bar .app-bar__menu {
      display: none;
    }
   
    .app-bar .app-bar__navigation {
      position: static;
      width: 100%;
      padding: 0;
    }

    .app-bar .app-bar__navigation ul {
      display: flex;
      justify-content: flex-start; /* Align items to the start */
    }
   
    .app-bar .app-bar__navigation ul li {
      display: inline-block;
      margin-bottom: 0; 
      margin-right: 50px; 
    }
   
    .app-bar .app-bar__navigation ul li a {
      display: inline-block;
      width: 44px;
      margin-right: 50px;
      text-align: center;
      margin: 0;
      margin: 0;
    }
   
    .restos {
      grid-template-columns: 1fr 1fr;
    }
   
    .resto {
      grid-template-columns: auto 1fr;
    }
   
    .resto .resto__title {
      grid-column-start: 1;
      grid-column-end: 3;
    }
   
    .resto .resto__overview {
      grid-column-start: 1;
      grid-column-end: 3;
    }
  }
   
  @media screen and (min-width: 800px) {
    .app-bar .app-bar__brand h1 {
      font-size: 2em;
    }
  }
   
  @media screen and (min-width: 850px) {
    .restos {
      grid-template-columns: repeat(3, 1fr);
    }
  }
   
  @media screen and (min-width: 1200px) {
    .restos {
      grid-template-columns: repeat(4, 1fr);
    }
  }
   
  @media screen and (min-width: 1600px) {
    .restos {
        grid-template-columns: repeat(5, 1fr);
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;IACI;MACE,+BAA+B;MAC/B,iBAAiB;IACnB;;IAEA;MACE,gBAAgB;IAClB;;IAEA;MACE,aAAa;IACf;;IAEA;MACE,gBAAgB;MAChB,WAAW;MACX,UAAU;IACZ;;IAEA;MACE,aAAa;MACb,2BAA2B,EAAE,6BAA6B;IAC5D;;IAEA;MACE,qBAAqB;MACrB,gBAAgB;MAChB,kBAAkB;IACpB;;IAEA;MACE,qBAAqB;MACrB,WAAW;MACX,kBAAkB;MAClB,kBAAkB;MAClB,SAAS;MACT,SAAS;IACX;;IAEA;MACE,8BAA8B;IAChC;;IAEA;MACE,+BAA+B;IACjC;;IAEA;MACE,oBAAoB;MACpB,kBAAkB;IACpB;;IAEA;MACE,oBAAoB;MACpB,kBAAkB;IACpB;EACF;;EAEA;IACE;MACE,cAAc;IAChB;EACF;;EAEA;IACE;MACE,qCAAqC;IACvC;EACF;;EAEA;IACE;MACE,qCAAqC;IACvC;EACF;;EAEA;IACE;QACI,qCAAqC;IACzC;EACF","sourcesContent":["@media screen and (min-width: 650px) {\r\n    .app-bar {\r\n      grid-template-columns: 1fr auto;\r\n      padding: 8px 32px;\r\n    }\r\n   \r\n    .app-bar .app-bar__brand h1 {\r\n      font-size: 1.5em;\r\n    }\r\n   \r\n    .app-bar .app-bar__menu {\r\n      display: none;\r\n    }\r\n   \r\n    .app-bar .app-bar__navigation {\r\n      position: static;\r\n      width: 100%;\r\n      padding: 0;\r\n    }\r\n\r\n    .app-bar .app-bar__navigation ul {\r\n      display: flex;\r\n      justify-content: flex-start; /* Align items to the start */\r\n    }\r\n   \r\n    .app-bar .app-bar__navigation ul li {\r\n      display: inline-block;\r\n      margin-bottom: 0; \r\n      margin-right: 50px; \r\n    }\r\n   \r\n    .app-bar .app-bar__navigation ul li a {\r\n      display: inline-block;\r\n      width: 44px;\r\n      margin-right: 50px;\r\n      text-align: center;\r\n      margin: 0;\r\n      margin: 0;\r\n    }\r\n   \r\n    .restos {\r\n      grid-template-columns: 1fr 1fr;\r\n    }\r\n   \r\n    .resto {\r\n      grid-template-columns: auto 1fr;\r\n    }\r\n   \r\n    .resto .resto__title {\r\n      grid-column-start: 1;\r\n      grid-column-end: 3;\r\n    }\r\n   \r\n    .resto .resto__overview {\r\n      grid-column-start: 1;\r\n      grid-column-end: 3;\r\n    }\r\n  }\r\n   \r\n  @media